      transition: height 0.4s cubic-bezier(1, 0, 0, 1) 0.4s, background-color 0.1s, -webkit-transform 0.4s;
      transition: transform 0.4s, height 0.4s cubic-bezier(1, 0, 0, 1) 0.4s, background-color 0.1s;
